header {
  display: flex;
  gap: 45px;
  align-items: center;
  justify-content: center;
  padding: 16px;

  ul {
    display: flex;
    gap: 28px;
    padding: 0;
    font-weight: bold;
  }
}

nav ul {
  list-style-type: none;
  font-weight: 500;
  font-size: 18px;
}

a {
  text-decoration: none;
  color: black;
}

footer {
  padding: 16px;
  display: flex;
  gap: 45px;
  justify-content: center;
  align-items: center;
  background-color: #495E57;
  color: white;

  div {
    display: flex;
    gap: 40px;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  a {
    text-decoration: none;
    color: white;
  }

  section {
    display: block;
  }

  li {
    padding-bottom: 16px;
  }
}

h3 {
  text-transform: uppercase;
  font-size: 20px;
  font-weight: bolder;
  color: #F4CE14
}

h1 {
  font-size: 64px;
  font-weight: 600;
  margin: 0;
}

h2 {
  margin-top: 0;
  font-size: 40px;
}

section {

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 50px;

  >div {
    max-width: 900px;
    padding: 16px;
    display: flex;
    justify-content: space-between;
    width: 100%;
    position: relative;
    margin-bottom: 24px;
    box-sizing: border-box;
  }
}

.hero-section {
  background-color: #495E57;
  color: white;
  display: flex;
  justify-content: center;
  margin-bottom: 200px;
  flex-direction: row;

  h2 {
    color: #F4CE14
  }

  .hero-text {
    max-width: 400px;
  }

  img {
    border-radius: 16px;
    position: absolute;
    right: 0;
  }

  p {
    font-size: 18px;
    font-weight: 600;
  }
}

img {
  object-fit: cover;
}



.button {
  background-color: #F4CE14;
  border-radius: 16px;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  color: black;
  padding: 16px;
  padding-inline: 24px;
  display: inline-block;
  height: fit-content;
  border: none;
  cursor: pointer;
}


.specials {
  display: flex;
  justify-content: space-between;
}

.dish {
  width: 260px;
  background-color: #eee;
  border-top-left-radius: 16px;
  border-top-right-radius: 16px;
  display: flex;
  flex-direction: column;

  figure {
    padding: 0;
    margin: 0;
  }

  img {
    border-top-left-radius: 16px;
    border-top-right-radius: 16px;
  }

  >div {
    padding: 16px;
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  a {
    margin-top: auto;
    font-weight: bold;
    font-size: 16px;
  }
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.price {
  color: #EE9972;
  font-weight: 600;
  font-size: 16px;
}

.card-title {
  font-weight: bold;
  font-size: 18px;
  color: black;
}

p {
  font-size: 16px;
  font-weight: normal;
}

.rating {
  color: #EE9972;
  font-size: 24px;
}

.testimonials {
  background-color: #495E57;
  color: white;
  padding: 68px;
}

.testimonials>div {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 25px;

  article {
    background-color: #eee;
    color: black;
    border-radius: 16px;
    padding: 16px;
  }

  h2 {
    grid-column: 1 / -1;
    text-align: center;
  }
}

.chicago {
  background-color: white;
  color: black;

  h2 {
    color: black;
  }

  .restaurant-chef {
    right: 0;
    top: 0;
  }

  .restaurant {
    right: 170px;
    top: 100px;
  }
}

label {
  font-size: 18px;
  font-weight: 500;
}

input,
select {
  border-radius: 16px;
  color: #495E57;
  padding: 20px;
  border-color: rgba(51, 51, 51, 0.25);
  background-color: white;
}

form {
  width: 100%;
  display: grid;
  gap: 20px;
}

h1,
h2 {
  font-family: "Markazi Text", serif;
  font-family: serif;
}

.without-image {
  margin-bottom: 24px;
}

.confirmation {
  >div {
    display: flex;
    align-items: baseline;
    gap: 16px
  }

  h3 {
    color: black;
  }
}

div.error {
  color: red;
}

input.error {
  border-color: red;
}

.menu-btn {
  display: none;
}

.desktop-nav {
  display: block;
}

nav {
  flex-direction: row;
  gap: 24px;
}

.menu-overlay {
  display: none;
}

@media (max-width: 960px) {
  header {
    justify-content: space-between;

    nav {
      display: flex;
      flex-direction: column;
      gap: 16px;
    }

    .desktop-nav {
      display: none;
    }

    .menu-btn {
      display: block;
      background: none;
      border: none;
      font-size: 24px;
      cursor: pointer;
    }


  }

  .menu-drawer {
    background: white;
    width: 75%;
    max-width: 300px;
    height: 100%;
    padding: 24px;
    animation: slideIn 0.3s ease-out;

    ul {
      gap: 24px;
      display: flex;
      flex-direction: column;
    }
  }

  .menu-overlay {
    display: block;
    position: fixed;
    inset: 0;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1000;
  }

  footer {
    flex-direction: column;
    gap: 20px;

    div {
      flex-direction: column;
    }
  }

  .hero-section {
    >div {
      align-items: end;
    }

    img {
      width: 150px;
      height: 200px;
      position: static;
    }
  }

  .specials {
    flex-direction: column;
    gap: 16px;
  }

  .dish {
    width: 100%;
    border-radius: 16px;
    display: flex;
    flex-direction: row-reverse;

    figure {
      margin: auto;
    }

    img {
      border-radius: 16px;
      max-width: 100%;
    }
  }

  .testimonials {
    padding-inline: 0;

    >div {
      gap: 16px;
      display: flex;
      flex-direction: column;
    }
  }

  .chicago {
    >div {
      display: grid;
      grid-template-columns: repeat(2, auto);
      grid-template-rows: repeat(2, auto);
      justify-content: center;
      gap: 16px
    }

    .hero-text {
      grid-column: 1 / -1;
    }

  }
}

@media (max-width: 400px) {

  .hero-section {
    >div {
      align-items: end;
    }

    img {
      width: 100px;
      height: 125px;
      position: static;
    }
  }
}

@keyframes slideIn {
  from {
    transform: translateX(-100%);
  }

  to {
    transform: translateX(0);
  }
}